"use client";
import React from "react";
import { 
  User, MapPin, Briefcase, GraduationCap, 
  PenLine, Mail, CalendarDays, BadgeCheck 
} from "lucide-react";

// Mock Data Interface for Teacher
interface TeacherProfileData {
  personal: {
    dob: string;
    gender: string;
    nationality: string;
    state_of_origin: string;
    marital_status: string;
    religion: string;
  };
  contact: {
    address: string;
    city: string;
    email: string;
    phone: string;
  };
  professional: {
    qualifications: string; // e.g. "B.Ed, MSc Mathematics"
    subjects: string[];     // e.g. ["Maths", "Physics"]
    classes_assigned: string[]; // e.g. ["SSS 1", "SSS 2"]
    experience: string;     // e.g. "8 Years"
  };
  employment: {
    employee_id: string;
    date_joined: string;
    status: "Active" | "On Leave" | "Suspended";
    role: string;           // e.g. "Senior Teacher"
    next_of_kin: string;
    next_of_kin_phone: string;
  };
}

export default function TeacherProfileDetails({ data }: { data: TeacherProfileData }) {
  if (!data) return null;

  return (
    <div className="space-y-8 py-4">
      
      {/* Header */}
      <div className="flex justify-between items-center pb-6 border-b border-zinc-100">
        <div>
          <h2 className="text-xl font-medium text-zinc-900">Teacher Profile</h2>
          <p className="text-sm text-zinc-500">Staff record and professional background</p>
        </div>
        <button className="flex items-center gap-2 text-sm text-zinc-600 border border-zinc-200 px-4 py-2 rounded-lg hover:bg-zinc-50 transition-colors">
          <PenLine size={14} /> 
          <span>Edit Profile</span>
        </button>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-x-12 gap-y-10">
        
        {/* SECTION 1: PERSONAL INFO */}
        <Section title="Identity" icon={<User size={16} />}>
           <div className="grid grid-cols-2 gap-y-6">
              <InfoItem label="Date of Birth" value={data.personal.dob} />
              <InfoItem label="Gender" value={data.personal.gender} />
              <InfoItem label="Nationality" value={data.personal.nationality} />
              <InfoItem label="State of Origin" value={data.personal.state_of_origin} />
              <InfoItem label="Marital Status" value={data.personal.marital_status} />
              <InfoItem label="Religion" value={data.personal.religion} />
           </div>
        </Section>

        {/* SECTION 2: CONTACT INFO */}
        <Section title="Reach" icon={<MapPin size={16} />}>
           <div className="space-y-6">
              <InfoItem label="Residential Address" value={data.contact.address} />
              <div className="grid grid-cols-2">
                <InfoItem label="City" value={data.contact.city} />
                <InfoItem label="Phone Number" value={data.contact.phone} isAccent />
              </div>
              <InfoItem label="Email Address" value={data.contact.email} icon={<Mail size={12}/>} />
           </div>
        </Section>

        {/* SECTION 3: PROFESSIONAL / ACADEMIC */}
        <Section title="Portfolio" icon={<GraduationCap size={16} />}>
           <div className="space-y-6">
              <InfoItem label="Qualifications" value={data.professional.qualifications} />
              <InfoItem label="Teaching Experience" value={data.professional.experience} />
              
              <div className="grid grid-cols-1 gap-4">
                  <div>
                    <span className="text-[10px] font-bold text-zinc-400 mb-2 block uppercase tracking-wider">
                      Subjects Handled
                    </span>
                    <div className="flex flex-wrap gap-2">
                      {data.professional.subjects.map((sub) => (
                        <span key={sub} className="text-xs font-medium bg-zinc-50 text-zinc-700 px-2 py-1 rounded border border-zinc-100">
                          {sub}
                        </span>
                      ))}
                    </div>
                  </div>
                  
                  <div>
                    <span className="text-[10px] font-bold text-zinc-400 mb-2 block uppercase tracking-wider">
                      Assigned Classes
                    </span>
                    <div className="flex flex-wrap gap-2">
                      {data.professional.classes_assigned.map((cls) => (
                        <span key={cls} className="text-xs font-medium bg-zinc-50 text-zinc-700 px-2 py-1 rounded border border-zinc-100">
                          {cls}
                        </span>
                      ))}
                    </div>
                  </div>
              </div>
           </div>
        </Section>

        {/* SECTION 4: EMPLOYMENT DETAILS */}
        <Section title="Employment" icon={<Briefcase size={16} />}>
           <div className="grid grid-cols-2 gap-y-6">
              <InfoItem label="Employee ID" value={data.employment.employee_id} />
              <InfoItem label="Date Joined" value={data.employment.date_joined} icon={<CalendarDays size={12}/>} />
              
              <div className="col-span-2">
                 <InfoItem label="Current Role" value={data.employment.role} />
              </div>

              <div className="col-span-2 pt-2 border-t border-zinc-50">
                 <div className="grid grid-cols-2 gap-4">
                    <InfoItem label="Next of Kin" value={data.employment.next_of_kin} />
                    <InfoItem label="Emergency Phone" value={data.employment.next_of_kin_phone} isAccent />
                 </div>
              </div>

              <div className="col-span-2">
                 <span className="text-[10px] font-bold text-zinc-400 mb-1 block uppercase tracking-wider">Status</span>
                 <span className={`inline-flex items-center gap-1.5 px-2.5 py-1 rounded text-xs font-bold uppercase tracking-wide ${
                    data.employment.status === "Active" ? "bg-emerald-50 text-emerald-600" : "bg-zinc-100 text-zinc-500"
                 }`}>
                    <BadgeCheck size={12} /> {data.employment.status}
                 </span>
              </div>
           </div>
        </Section>

      </div>
    </div>
  );
}

// --- MINIMAL HELPERS (Unchanged) ---

function Section({ title, icon, children }: { title: string, icon: any, children: React.ReactNode }) {
  return (
    <div className="flex flex-col h-full">
      <div className="flex items-center gap-2 mb-6 text-zinc-400">
        {icon}
        <h3 className="font-bold uppercase tracking-[0.15em] text-[11px]">{title}</h3>
      </div>
      <div className="pl-6 border-l border-zinc-100 h-full">
        {children}
      </div>
    </div>
  );
}

function InfoItem({ label, value, isAccent, icon }: { label: string, value: string, isAccent?: boolean, icon?: any }) {
  return (
    <div>
      <span className="text-[10px] font-bold text-zinc-400 mb-1.5 flex items-center gap-1 uppercase tracking-wider">
        {icon} {label}
      </span>
      <p className={`text-[13px] leading-relaxed ${isAccent ? "text-indigo-600 font-semibold" : "text-zinc-700 font-normal"}`}>
        {value || "â€”"}
      </p>
    </div>
  );
}